import Dialog from '../../wxcomponents/vant-weapp/dialog/dialog'
export default {
	data() {
		return {
			curProg: 0,
			curProgress: 0,
			programs: [],
			leftOb: 10,
			picks: []
		};
	},
	onLoad() {
		const db = wx.cloud.database()
		db.collection('programs').field({
			_id: true,
			programId: true,
			title: true,
			imgUrl: true,
			disc: true
		}).get().then(res => {
			console.log(res)
			this.programs = res.data.map(item => {
				item.ob = 0
				return item
			})
		})
	},
	methods: {
		handleChange(current, source){
			this.curProgress = current * 9.0909
		},
		decrease(e){
			const index = e.currentTarget.dataset.index
			if(this.programs[index].ob>0 ){
				this.programs[index].ob--;
				this.leftOb++;
				this.picks = this.programs.filter(item=>{
					return item.ob>0
				})
			}
		},
		increase(e){
			const index = e.currentTarget.dataset.index
			if(this.leftOb>0){
				this.programs[index].ob++;
				this.leftOb--;
				this.picks = this.programs.filter(item=>{
					return item.ob>0
				})
			}
		},
		onEnd(){
			Dialog.confirm({
				title: "结束节目竞猜",
				message: "结束后，将不能再进行其他竞猜行为。",
				cancelButtonText: '再想想',
			})	
		},
		onConfirm(){
			const myPicks = JSON.stringify(this.picks)
			try {
				uni.setStorageSync('myPicks', myPicks)
				uni.setStorageSync('leftOb', this.leftOb)
			} catch(e) {
				console.log(e)
			}
			uni.navigateTo({
				url: `/pages/home/home`,
			})
		},
		onChange(e){
			if(e.detail.source==="touch"){
				this.curProgress = Math.round(e.detail.current/9.0909)				
			}
			console.log(e.detail.current)
		}
	}
}
